<template>

        <!-- Carousel Banner start -->         
        <section class="banner relative"> 
            <div class="owl-carousel owl-theme slider-1"> 


                <div v-for="item, index in this.content.items" :key="index" class="item slider-imgs flex-center" :style="`background-image: url('${item.image.url}');`" data-overlay="2"> 
                    <div class="banner-content z-5"> 
                        <h1 class="banner-main-head white f-700 mb-40 mb-sm-25 mb-xs-15"><a href="">{{item.title}}</a></h1> 
                        <span v-for="tag, index in item.tags.split(/\r?\n/)" :key="index" class="label label-default">{{tag}}</span>
                    </div>                     
                </div>                
          


            </div>             


            <ul class="social-icons banner-social-icons d-none d-sm-block white"> 
                <li> 
                    <a href=""><i class="fab fa-facebook-f"></i></a> 
                </li>                 
                <li> 
                    <a href=""><i class="fab fa-twitter"></i></a> 
                </li>                 
                <li> 
                    <a href=""><i class="fab fa-linkedin-in"></i></a> 
                </li>                 
                <li> 
                    <a href=""><i class="fab fa-google-plus-g"></i></a> 
                </li>                 
            </ul>             
            <a href="" class="btn btn-border-out banner-hire z-5">JOIN US</a> 
        </section>         
        <!-- Carousel Banner end -->      
        
</template>


<script>
export default {
  props: ["content"],
  mounted() {
    var banner1 = $(".slider-1");
    banner1.owlCarousel({
      items: 1,
      loop: true,
      autoplay: true,
      autoplayTimeout: 6000,
      autoplayHoverPause: true,
      margin: 0,
      nav: true,
      navText: [
        "<i class='fas fa-long-arrow-alt-left'></i>",
        "<i class='fas fa-long-arrow-alt-right'></i>",
      ],
      dots: false,
    });
    banner1.on("changed.owl.carousel", function(event) {
      var item = event.item.index - 2; // Position of the current item
      $(".banner-content").removeClass("wow fadeInUp");
      $(".owl-item")
        .not(".cloned")
        .eq(item)
        .find(".banner-content")
        .addClass("wow fadeInUp");
    });

    console.log(this.content.items)
  }
}
</script>
