<template>
  <div>
    
    <navBar />
    <searchModal />

    <component v-for="entry, index in homePageData" :key="index" :is="getComponent(entry.slice_type)" :content="entry" />

    <!-- <footerSection /> -->
  </div>
</template>

<script>
import carousel from "@/components/carousel.vue";
// import footerSection from "@/components/footer.vue";
import ctaBanner from "@/components/ctaBanner.vue";
import blog from "@/components/blog.vue";
import testimonials from "@/components/testimonials.vue";
import about from "@/components/about.vue";

import halfRow from "@/components/mediaRows/halfRow.vue";
import thirdRow from "@/components/mediaRows/thirdRow.vue";
import quarterRow from "@/components/mediaRows/quarterRow.vue";

import navBar from "@/components/navBar.vue";
import searchModal from "@/components/searchModal.vue";
import loader from "@/components/loader.vue";

export default {
  name: "Home",
  components: {
    navBar,
    searchModal,
    carousel,
    halfRow,
    thirdRow,
    quarterRow,
    // footerSection,
    ctaBanner,
    blog,
    testimonials,
    about,
    loader,
  },
   data() {
    return {
      homePageData: false
    };
  },
  methods: {
    getComponent(sliceType) {
      const table =  {
        "half_row_images": halfRow,
        "third_row_images": thirdRow,
        "quarter_row_images": quarterRow,
        "carousel": carousel,
        "about_section": about
      }
      return table[sliceType]
    }
  },
  async created() {
    const response = await this.$prismic.client.query(
      this.$prismic.Predicates.at("document.type", "home_page")
    );

    const homePageData = response.results[0].data.body;
    this.homePageData = homePageData;

    console.log("API response: ", homePageData);
  }
};
</script>
