<template>
  <section class="work-gallery">
    <div class="container-fluid p-0">
      <div v-if="homePageData" class="row no-gutters">
        
        <!-- this gallery will only be used on the homepage, OR the container/row no-gutters will be used on every row template. -->

        <component v-for="entry, index in homePageData" :key="index" :is="getComponent(entry.slice_type)">
        </component>

        <!-- load more -->

        <!-- <div class="col-lg-12"> -->
        <!-- <a href="" class="btn btn-full bg-yellow rbt-slab">Load More..</a> -->
        <!-- </div> -->
      </div>
    </div>
  </section>
</template>

<script>
import halfRow from "@/components/mediaRows/halfRow.vue";
import thirdRow from "@/components/mediaRows/thirdRow.vue";
import quarterRow from "@/components/mediaRows/quarterRow.vue";

export default {
  props: ["content"],
  components: {
    halfRow,
    thirdRow,
    quarterRow,
  },
  data() {
    return {
      homePageData: false
    };
  },
  methods: {
    getComponent(sliceType) {
      const table =  {
        "half_row_images": halfRow,
        "third_row_images": thirdRow,
        "quarter_row_images": quarterRow
        // "carousel": ,
        // "about_section": null
      }
      return table[sliceType]
    }
  },
  async created() {
    const response = await this.$prismic.client.query(
      this.$prismic.Predicates.at("document.type", "home_page")
    );

    const homePageData = response.results[0].data.body;
    this.homePageData = homePageData;

    console.log("API response: ", homePageData);

    window.itsReady = true

    /* filter example. */
    // const carouselFilter = homePageData.filter((item) => {
    //   return item.slice_type.includes("carousel");
    // });
    // console.log("filter results: ", carouselFilter);
    // this.carouselContent = carouselFilter;

  },
};
</script>