import { createRouter, createWebHistory } from 'vue-router'
import Home from '../views/Home.vue'
import mediumPage from '../views/mediumPage.vue'
import aboutPage from '../views/aboutPage.vue'

const routes = [
  {
    path: '/',
    alias: '/index.html',
    name: 'Home',
    component: Home
  },
  { 
    // todo: what is the best way to load different content for each alias to the same component? can you pull from route name? subroutes? have to see the docs.
    path: '/index_mixedmedia',
    alias: ['/index_mixedmedia.html', '/index_painting.html', '/index_drawing.html', '/index_sculpture.html', '/index_photography.html'],
    name: 'MediumPage',
    component: mediumPage
  },
  {
    path: '/about-mixed-media.html',
    name: 'aboutPage',
    component: aboutPage
  }
  // {
    // path: '/about',
    // name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    // component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  // }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
