<script>
export default {
  mounted() {

    $.fn.followTo = function() {
      let $window = $(window);

      let getPositions = function() {
        if (
          $(".pin-to-top").outerHeight() + $(".sidebar-left").outerHeight() >
          $(document).height() - $(window).scrollTop()
        ) {
          $(".sidebar-left").css({
            transform: `translateY(-${$(".pin-to-top").outerHeight() +
              $(".sidebar-left").outerHeight() -
              ($(document).height() - $(window).scrollTop())}px)`,
          });
        } else {
          $(".sidebar-left").css({
            transform: "none",
          });
        }
      }

      $window.scroll(function(e) {
        getPositions();
      });

      $(".has-sub").click(function() {
        setTimeout(getPositions, 200);
      });


    };

    $(".sidebar-left").followTo();
    // .followTo($('.pin-to-top').position().top);
  },
};
</script>
